import React, { Component } from "react";
import gsap from "gsap";
import "./SCSS/Landingpage.scss";
import { Typography } from "@material-ui/core";
import { Button, Container } from "@material-ui/core";
import wallpaper from "./img/pcwall3.png";
import CloseIcon from "@material-ui/icons/Close";
export default class Landingpage extends Component {
  handleClick = () => {
    const layer = document.querySelector(".layer");
    const closeicons = document.querySelector(".closeicons");
    layer.classList.toggle("slidenotif");
  };

  render() {
    return (
      <div>
        <div className="container">
          <div className="text">
            <h1>Hello World</h1>
            <p>Mari belajar react js dengan mudah!</p>
            <p className="ketentuan">*Latihan Project React Javascript</p>
            <Button variant="contained" className="klikme" onClick={this.handleClick}>
              Click Me
            </Button>
          </div>
          <div className="img">
            <img src={wallpaper} alt="" />
          </div>
          <div className="layer">
            <CloseIcon className="closeicons" onClick={this.handleClick} />
            <h2 className="text">Thank's Click Me</h2>
          </div>
        </div>
      </div>
    );
  }
}
