import React, { useEffect, useRef } from "react";
import gsap from "gsap";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import "./SCSS/Homepage.scss";
import CloseIcon from "@material-ui/icons/Close";
import MenuIcon from "@material-ui/icons/Menu";
import Landingpage from "./Landingpage";
import { Button } from "@material-ui/core";
import About from "./About";
import Project from "./Project";
export default function Homepage() {
  function slideclick() {
    const linkItems = document.querySelector(".link-items");
    const closeBar = document.querySelector(".close");
    linkItems.classList.toggle("slidebar");
    closeBar.classList.toggle("slidebar");
  }
  const navbar = useRef(null);

  useEffect(() => {
    gsap.from(navbar.current, {
      duration: 1,
      translateY: "-15vh",
      opacity: 0,
      ease: "ease-in",
    });
  }, []);
  return (
    <Router>
      <div>
        <nav className="navbar" ref={navbar}>
          <div className="logo">
            <Link to="/">
              <h1>Muhammad Fajri</h1>
            </Link>
          </div>
          <div className="menu-bar" onClick={slideclick}>
            <MenuIcon />
          </div>
          <ul className="link-items">
            <div className="close" onClick={slideclick}>
              <CloseIcon />
            </div>
            <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/about">About</Link>
            </li>
            <li>
              <Link to="/project">Project</Link>
            </li>
          </ul>
        </nav>

        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/project">
            <Project />
          </Route>
          <Route path="/">
            <Landingpage />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

function Home() {
  return <h2>Home</h2>;
}

function Users() {
  return <h2>Users</h2>;
}
